.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(135deg, #6e45e2 0%, #88d3ce 100%);
    font-family: 'Poppins', sans-serif;
  }
  
  .login-card {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  
  .login-title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 30px;
    color: #333;
    letter-spacing: 1px;
  }
  
  .form-group {
    margin-bottom: 20px;
    text-align: left;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
    color: #555;
  }
  
  .form-group input {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid #ddd;
    border-radius: 30px;
    box-sizing: border-box;
    font-size: 16px;
    background-color: #f7f7f7;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    outline: none;
  }
  
  .form-group input:focus {
    border-color: #6e45e2;
    box-shadow: 0 0 8px rgba(110, 69, 226, 0.2);
    background-color: #ffffff;
  }
  
  .login-button {
    width: 100%;
    padding: 12px;
    background: linear-gradient(135deg, #6e45e2 0%, #88d3ce 100%);
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .login-button:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(110, 69, 226, 0.3);
  }
  
  .login-button:active {
    transform: translateY(0);
    box-shadow: none;
  }
  
  .login-card::before {
    content: '';
    position: absolute;
    top: -50px;
    right: -50px;
    width: 150px;
    height: 150px;
    background: rgba(110, 69, 226, 0.2);
    border-radius: 50%;
    z-index: 1;
  }
  
  .login-card::after {
    content: '';
    position: absolute;
    bottom: -50px;
    left: -50px;
    width: 150px;
    height: 150px;
    background: rgba(136, 211, 206, 0.2);
    border-radius: 50%;
    z-index: 1;
  }
  
  .login-card {
    position: relative;
    z-index: 2;
  }
  
  .form-group input::placeholder {
    color: #bbb;
  }
  
  @media (max-width: 480px) {
    .login-card {
      padding: 30px;
    }
  
    .login-title {
      font-size: 24px;
    }
  
    .login-button {
      padding: 10px;
    }
  }
  